<template>
	<view>
		<view class="top" @tap="upload">
			<image :src="pic" mode=""></image>
		</view>
		<!-- <uni-easyinput v-model="pswd" type="password"  placeholder="请输入登录密码"></uni-easyinput> -->
		<view class="list">
			<view class="item">
				<uni-easyinput v-model="phone" type="text" :inputBorder="false" placeholder="手机号"
					placeholderStyle="color:#808080;font-size: 28rpx;">
				</uni-easyinput>
			</view>
			<view class="item">
				<uni-easyinput v-model="pswd" type="password" :inputBorder="false" placeholder="请输入登录密码"
					placeholderStyle="color:#808080;font-size: 28rpx;"></uni-easyinput>
			</view>
			<view class="item">
				<uni-easyinput v-model="pswd" type="text" :inputBorder="false" placeholder="请输入用户名 该用户名将用于账户登录"
					placeholderStyle="color:#808080;font-size: 28rpx;"></uni-easyinput>
			</view>
			<view class="item">
				<uni-easyinput v-model="pswd" type="text" :inputBorder="false" placeholder="请输入昵称"
					placeholderStyle="color:#808080;font-size: 28rpx;"></uni-easyinput>
			</view>
			<view class="item">
				<uni-easyinput v-model="pswd" type="text" :inputBorder="false" placeholder="请输入验证码"
					placeholderStyle="color:#808080;font-size: 28rpx;"></uni-easyinput>
				<view class="huoqu">获取验证码</view>
			</view>
		</view>
		<view class="footer">
			<view class="center" @tap="register">注 册</view>
		</view>
	</view>
</template>

<script>
	import WxValidate from "../../../utils/WxValidate.js"
	import BASE_URL from "../../../api/baseurl.js"
	export default {
		name: "register",
		data() {
			return {
				pic: "../../../static/icons/leju-logo.png",
				phone: "",
				pswd: ""
			};
		},
		methods: {
			
			// 上传图片
			upload() {
				var that = this;
				uni.chooseImage({
					count: 1,
					sizeType: ["original"],
					sourceType: ["album "],
					success(res) {
						console.log(res);
						uni.uploadFile({
							url: BASE_URL + "/lejuClient/login/uploadAvatar",
							filePath: res.tempFilePaths[0],
							name: "file",
							success(res1) {
								// console.log(res1)
								that.pic = JSON.parse(res1.data).data.fileUrl
							}
						})
					}
				})
			},
			// 注册
			register(){
				
				 let params = e.detail.value;
				 if (!this.WxValidate.checkForm(params)) {
				  //表单元素验证不通过，此处给出相应提示
				    let error = this.WxValidate.errorList[0];
				    switch (error.param) {
				        case "Name":
				          //TODO
				          break;
				        case "sex":
				         //TODO
				          break;
				        case "birthDate":
				         //TODO
				          break;
				        case "Card":
				         //TODO
				          break;
				
				      }
				  }
				 
				//验证通过，往下执行
			},
			// 表单验证
			initValidate() {
				let rules = {
					Name: {
						required: true,
						maxlength: 10
					},
			 	sex: {
						required: true,
						number: true
					},
					birthDate: {
						required: true,
						dateISO: true,
					},
					Card: {
						required: false,
						idcard: true
					}
				}

				let message = {
					Name: {
						required: '请输入姓名',
						maxlength: '名字不能超过10个字'
					},
					Card: {
						idcard: "请输入正确的身份证号码"
					},
					sex: {
						required: "请选择您的性别",
						number: '请您选择您的性别'
					},
					birthDate: {
						required: "请选择出生年月",
						dateISO: "请选择出生年月",
					},
				}
				//实例化当前的验证规则和提示消息
				this.WxValidate = new WxValidate(rules, message);
			}
		},
		onLoad(options) {
			this.initValidate();
		}
	}
</script>

<style lang="scss" scepod>
	.top {
		width: 160rpx;
		height: 160rpx;
		margin: 128rpx auto 72rpx;

		image {
			width: 100%;
			height: 100%;
			border-radius: 50%;
		}
	}

	.list {
		padding: 0 70rpx;

		.item {
			margin: 24rpx 0;
			padding: 32rpx;
			width: 546rpx;
			height: 36rpx;
			border-radius: 60rpx;
			border: 10rpx solid #e8e5e1;
			display: flex;
			justify-content: space-between;
			align-items: center;
			font-size: 38rpx;

			// box-shadow: 0 0 30px 0 rgb(43 86 112 / 10%);
			.huoqu {
				color: rgba(0, 0, 0, .7);
				font-size: 24rpx;
				line-height: 100rpx;
			}
		}
	}

	.footer {
		width: 100%;
		height: 100rpx;
		position: fixed;
		bottom: 40rpx;
		left: 0;

		.center {
			width: 600rpx;
			height: 100%;
			background-color: #354e44;
			margin: 0 auto;
			border-radius: 50rpx;
			color: #fff;
			line-height: 100rpx;
			text-align: center;
			font-size: 32rpx;
		}
	}
</style>
